/*                                  FILTERS.JS                         */

.filter-wrapper{
  display:flex;
  height: 100%;
  max-height:9vh;
  grid-area: fbtn;
  align-items:center;
}

.time-wrapper{
  display:flex;
  flex-direction:column;
  height:50%;
