  border-bottom-left-radius:50px;
  display:flex;
  align-items:center;
  justify-content:center;
}

.diet-dark{
  background-color:#DC8D09;
  display:flex;
  min-width: 20vw;
  height:4.58vh;
  border-top-right-radius:50px;
  border-bottom-right-radius:50px;
  align-items:center;
  justify-content:space-evenly;
}

.gluten-btn{
  height:15px;
  width:15px;
  background-color:transparent;
  border:#FFF4DA solid 2px;
  border-radius:5px;
  cursor:pointer;
}

.veg-btn{
  height:15px;
  width:15px;
  background-color:transparent;
  border:#FFF4DA solid 2px;
  border-radius:5px;
  cursor:pointer;

}
.filter-text{
  font:20px Montserrat, sans-serif;
  color:#FFF4DA;

}

.filter-subtext{
