/*                           MEDIA QUERIES                       */
/*                            DESKTOP                            */



@media screen and (max-width: 1220px){
  .diet-dark{
    background-color:#DC8D09;
    display:flex;
    min-width: 25vw;
    height:4.58vh;
    border-top-right-radius:50px;
    border-bottom-right-radius:50px;
    align-items:center;
    justify-content:space-evenly;
  }
}

@media screen and (max-width: 1000px) {
  .recipe-display{
    min-width:40vw;
  }
  .recipe-card{
    max-width:240px;
  }
  .recipe-card{
    font-size:16px;
  }
}

@media screen and (max-width: 950px) {
  .green-instructions{
    font-size:18px;
  }
  h4{
    font-size:28px;
    margin-top:20%;
  }
  .green-upper-wrapper{
    padding-left:20px;
    padding-right:20px;
  }
  .ingredients-list{
    font-size:15px;
  }

  .instructions-list{
    font-size:15px;
  }
}


